import axios from "axios";
import { HandleLogoutOnTokenExpire } from "./Components/utils/Logout";

const accessToken = localStorage.getItem("p_teacher_auth_token");

export const base_url = "https://p-teacher-dev.herokuapp.com";

const instance = axios.create({
  baseURL: base_url,
  withCredentials: false,
  headers: {
    "Content-Type": "application/json",
  },
});

instance.interceptors.request.use(
  (config) => {
    config.headers.authorization = `Bearer ${accessToken}`;
    console.log(config);
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

//response interceptor to refresh token on receiving token expired error
instance.interceptors.response.use(
  (response) => {
    console.log(response);
    return response;
  },
  async (error) => {
    const originalRequest = error.config;
    let refreshToken = localStorage.getItem("refreshToken");
    console.log(error);
    if (
      error.response.status === 401 ||
      (error.response.status === 403 && !originalRequest._retry)
    ) {
      // <HandleLogoutOnTokenExpire logout={true} />;
      // console.log("hello");
      // localStorage.removeItem("p_teacher_auth_token");
      // localStorage.removeItem("p_teacher_refresh_token");
      originalRequest._retry = true;
      try {
        let result = await axios.post(`${base_url}/auth/refresh_token`, {
          refreshToken: refreshToken,
        });
        if (result.status === 200) {
          localStorage.setItem("p_teacher_auth_token", result.data.token);
          console.log("Access token refreshed!");
          return axios(originalRequest);
        }
      } catch (error) {
        return Promise.reject(error);
      }
    }
  }
);

export default instance;

