import { ActionTypes } from "./constants";

export const setClasses = (classes) => ({
  type: ActionTypes.SET_CLASSES,
  payload: classes,
});

export const setBoards = (boards) => ({
  type: ActionTypes.SET_BOARDS,
  payload: boards,
});

export const setSubjects = (subjects) => ({
  type: ActionTypes.SET_SUBJECTS,
  payload: subjects,
});

export const setClassId = (classId) => ({
  type: ActionTypes.SET_CLASSID,
  payload: classId,
});

export const setBoardId = (boardId) => ({
  type: ActionTypes.SET_BOARDID,
  payload: boardId,
});

export const setSubjectId = (subjectId) => ({
  type: ActionTypes.SET_SUBJECTID,
  payload: subjectId,
});

export const setUnits = (units) => ({
  type: ActionTypes.SET_UNITS,
  payload: units,
});

export const setUnit = (unit) => ({
  type: ActionTypes.SET_UNIT,
  payload: unit,
});

export const setChapters = (chapters) => ({
  type: ActionTypes.SET_CHAPTERS,
  payload: chapters,
});

export const setChapter = (chapter) => ({
  type: ActionTypes.SET_CHAPTER,
  payload: chapter,
});

export const setTopics = (topics) => ({
  type: ActionTypes.SET_TOPICS,
  payload: topics,
});

export const setTopic = (topic) => ({
  type: ActionTypes.SET_TOPIC,
  payload: topic,
});

