import React, { useState, useEffect } from "react";
import "./Chapters.css";
import { useLocation, useHistory } from "react-router-dom";
import Modal from "react-modal";
import Zoom from "react-reveal/Zoom";
import { FilePicker } from "react-file-picker";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import HandleLogout from "../utils/Logout";
import { createSelector } from "reselect";
import {
  makeSelectChapters,
  makeSelectedData,
  makeSelectUnit,
} from "../../AppStore/selectors";
import { useSelector, useDispatch } from "react-redux";
import axios from "../../Axios";
import { setChapter, setChapters } from "./../../AppStore/actions";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const chaptersData = [
  { id: 1, title: "chapter 1", img: "" },
  { id: 2, title: "chapter 2", img: "" },
  { id: 3, title: "chapter 3", img: "" },
];

const customStyles = {
  content: {
    height: "50vh",
    width: "45vw",
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    borderRadius: "20px",
  },
};

const stateSelector = createSelector(makeSelectedData, (selectedData) => ({
  selectedData,
}));

const unitSelector = createSelector(makeSelectUnit, (unit) => ({
  unit,
}));

const chaptersSelector = createSelector(makeSelectChapters, (chapters) => ({
  chapters,
}));

const actionChaptersDispatcher = (dispatch) => ({
  setChapters: (chapters) => dispatch(setChapters(chapters)),
});

const actionChapterDispatcher = (dispatch) => ({
  setChapter: (chapter) => dispatch(setChapter(chapter)),
});

function Chapters() {
  const { selectedData } = useSelector(stateSelector);
  const { unit } = useSelector(unitSelector);
  const { chapters } = useSelector(chaptersSelector);
  const { setChapters } = actionChaptersDispatcher(useDispatch());
  const { setChapter } = actionChapterDispatcher(useDispatch());
  const [open, setOpen] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);
  const [chapterName, setChapterName] = useState("");
  const [updateChapterName, setUpdateChapterName] = useState("");
  const [unitImage, setUnitImage] = useState("");
  const [updateChapterImage, setUpdateChapterImage] = useState("");
  const [updateChapterIndex, setUpdateChapterIndex] = useState(null);
  const [chapterReload, setChapterReload] = useState(false);

  const history = useHistory();
  const location = useLocation();
  const details = location.state;
  // console.log(location);

  useEffect(() => {
    async function fetchData() {
      try {
        const result = await axios({
          method: "GET",
          url: `/v1/auth/unit/${unit._id}/chapters?sort=ASC`,
        });
        if (result) {
          console.log(result);
          setChapters(result.data);
        }
      } catch (err) {
        console.log(err);
      }
    }
    fetchData();
    setChapterReload(false);
  }, [chapterReload === true]);

  const handleUpdateUnit = () => {
    const data = {
      id: updateChapterIndex,
      title: updateChapterName,
    };
    chapters[updateChapterIndex] = data;
    console.log(updateChapterImage);
    setOpenEdit(false);
  };

  const handleEditModel = (index) => {
    setUpdateChapterIndex(index);
    setOpenEdit(true);
  };

  const onFileErrorHandle = (errMsg) => {
    alert(errMsg);
  };

  const handleAddUnit = async () => {
    const data = {
      unitId: unit._id,
      chapterName: chapterName,
      topics: [],
    };
    chapters.push(data);
    try {
      const result = await axios({
        method: "POST",
        url: "/v1/auth/unit/chapter",
        data,
      });
      if (result) {
        setChapterReload(true);
        toast.success("Unit added Successfully");
      }
    } catch (error) {
      toast.error("Error occured");
      console.log(error);
    }
    setOpen(false);
  };

  const handleClick = (chapter) => {
    setChapter(chapter);
    history.push(`/subchapters`);
  };

  return (
    <div className="chapters">
      <div className="classSelection__header">
        <h1>Chapters</h1>
        <HandleLogout />
      </div>
      <div className="chaptersContainer">
        <div className="chaptersContainer__top">
          <p>class {selectedData.classId}</p>
          <p>{selectedData.boardId}</p>
          <p>{selectedData.subjectId}</p>
          <p>{unit.unit.name}</p>
          <button onClick={() => setOpen(true)}>Add New Chapter</button>
        </div>
        <div className="chaptersContainer__middle">
          {chapters &&
            chapters.map((chapter, index) => (
              <div className="chapters__row">
                <span>{index + 1}</span>
                <p onClick={() => handleClick(chapter)}>
                  {chapter.chapterName}
                </p>
                <div className="image"></div>
                <p onClick={() => handleEditModel(index)}>edit chapter</p>
              </div>
            ))}
        </div>
      </div>
      {open && (
        <Modal isOpen={true} style={customStyles}>
          <Zoom>
            <button onClick={() => setOpen(false)} className="close-modal">
              x
            </button>
            <div className="modalContent">
              <div className="modalContent__row">
                <label>Title :</label>
                <input
                  type="text"
                  value={chapterName}
                  onChange={(e) => setChapterName(e.target.value)}
                />
              </div>
              <div className="modalContent__row">
                <label>Featured Image :</label>
                <div>
                  <FilePicker
                    extensions={["jpg", "png", "gif", "jpeg"]}
                    onChange={(FileObject) => setUnitImage(FileObject)}
                    onError={(errMsg) => onFileErrorHandle(errMsg)}
                  >
                    <button>
                      <i className="fas fa-cloud-upload-alt"></i>Upload Image
                    </button>
                  </FilePicker>
                </div>
              </div>
              <button onClick={handleAddUnit}>Add Chapter</button>
            </div>
          </Zoom>
        </Modal>
      )}
      {openEdit && (
        <Modal isOpen={true} style={customStyles}>
          <Zoom>
            <button onClick={() => setOpenEdit(false)} className="close-modal">
              x
            </button>
            <div className="modalContent">
              <div className="modalContent__row">
                <label>New Title :</label>
                <input
                  type="text"
                  value={updateChapterName}
                  onChange={(e) => setUpdateChapterName(e.target.value)}
                />
              </div>
              <div className="modalContent__row">
                <label>New Featured Image :</label>
                <div>
                  <FilePicker
                    extensions={["jpg", "png", "gif", "jpeg"]}
                    onChange={(FileObject) => setUpdateChapterImage(FileObject)}
                    onError={(errMsg) => onFileErrorHandle(errMsg)}
                  >
                    <button>
                      <i className="fas fa-cloud-upload-alt"></i>Upload Image
                    </button>
                  </FilePicker>
                </div>
              </div>
              <button onClick={handleUpdateUnit}>Update Chapter</button>
            </div>
          </Zoom>
        </Modal>
      )}
      <ToastContainer
        position="bottom-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </div>
  );
}

export default Chapters;
