import { createSelector } from "reselect";

const activitiesState = (state) => state.activityReducer;

export const makeSelectClasses = createSelector(
  activitiesState,
  (activities) => {
    console.log(activities);
    return activities.classes;
  }
);

export const makeSelectBoards = createSelector(
  activitiesState,
  (activities) => activities.boards
);

export const makeSelectSubjects = createSelector(
  activitiesState,
  (activities) => activities.subjects
);

export const makeSelectedData = createSelector(
  activitiesState,
  (activities) => ({
    classId: activities.classId,
    boardId: activities.boardId,
    subjectId: activities.subjectId,
  })
);

export const makeSelectClassId = createSelector(
  activitiesState,
  (activities) => activities.classId
);

export const makeSelectBoardId = createSelector(
  activitiesState,
  (activities) => activities.boardId
);

export const makeSelectSubjectId = createSelector(
  activitiesState,
  (activities) => activities.subjectId
);

export const makeSelectUnits = createSelector(activitiesState, (activities) => {
  console.log(activities);
  return activities.units;
});

export const makeSelectUnit = createSelector(activitiesState, (activities) => {
  return activities.unit;
});

export const makeSelectChapters = createSelector(
  activitiesState,
  (activities) => {
    return activities.chapters;
  }
);

export const makeSelectChapter = createSelector(
  activitiesState,
  (activities) => {
    return activities.chapter;
  }
);

export const makeSelectTopics = createSelector(
  activitiesState,
  (activities) => {
    console.log(activities);
    return activities.topics;
  }
);

export const makeSelectTopic = createSelector(activitiesState, (activities) => {
  return activities.topic;
});
