import React, { useEffect } from "react";
import "./App.css";
import { Switch, Route } from "react-router-dom";
import MutlipleChoice from "./Components/multiple choice/MultipleChoice";
import MatchFollowing from "./Components/match following/MatchFollowing";
import DragNdDrop from "./Components/drag and drop/DragNdDrop";
import TrueOrFalse from "./Components/true or false/TrueOrFalse";
import VideoQNA from "./Components/Video QnA/VideoQNA";
import AudioQNA from "./Components/audio QnA/AudioQNA";
import MultipleChoiceMA from "./Components/multiple choice MA/MultipleChoiceMA";
import MeaningFullWord from "./Components/meaninig full word/MeaningFullWord";
import MeaningFullSentence from "./Components/meaning sentence/MeaningFullSentence";
import ArrangeNumbers from "./Components/arrange numbers/ArrangeNumbers";
import MoreAndLess from "./Components/more and less/MoreAndLess";
import MathTemplate from "./Components/match the math/MathTemplate";
import ShortQNA from "./Components/Short QNA/ShortQNA";
import AnswerTable1 from "./Components/answer table/AnswerTable1";
import FillInBlanks from "./Components/fill in the blanks/FillInBlanks";
import LoginPage from "./Components/login page/LoginPage";
import Subjects from "./Components/class subjects/Subjects";
import ClassSelection from "./Components/class selection/SelectClass";
import BoardSelection from "./Components/board selection/BoardSelection";
import Units from "./Components/unit selection/Units";
import Chapters from "./Components/chapters/Chapters";
import SubChapters from "./Components/sub chapters/SubChapter";
import SelectActivity from "./Components/select activity/SelectActivity";
import TableQuestions from "./Components/table question/TableQuestions";
import PrivateRoute from "./Components/routing/PrivateRoute";
import { useHistory } from "react-router-dom";

function App() {
  const authToken = localStorage.getItem("p_teacher_auth_token");
  const history = useHistory();
  useEffect(() => {
    if (!authToken) {
      history.push("/login");
    }
  }, [authToken, history]);
  return (
    <div className="App">
      <Switch>
        <PrivateRoute exact path="/" component={ClassSelection} />
        <PrivateRoute exact path="/boardselection" component={BoardSelection} />
        <Route exact path="/login" component={LoginPage} />

        <PrivateRoute exact path="/subjects" component={Subjects} />

        <PrivateRoute exact path="/units" component={Units} />

        <PrivateRoute exact path="/chapters" component={Chapters} />

        <PrivateRoute exact path="/subchapters" component={SubChapters} />

        <PrivateRoute exact path="/selectactivity" component={SelectActivity} />

        <PrivateRoute exact path="/multiplechoice" component={MutlipleChoice} />

        <PrivateRoute
          exact={true}
          path="/matchfollowing"
          component={MatchFollowing}
        />

        <PrivateRoute exact={true} path="/draganddrop" component={DragNdDrop} />

        <PrivateRoute
          exact={true}
          path="/trueorfalse"
          component={TrueOrFalse}
        />

        <PrivateRoute exact={true} path="/shortqna" component={ShortQNA} />

        <PrivateRoute exact={true} path="/videoqna" component={VideoQNA} />

        <PrivateRoute exact={true} path="/audioqna" component={AudioQNA} />

        <PrivateRoute
          exact={true}
          path="/multichoicemultianswers"
          component={MultipleChoiceMA}
        />

        <PrivateRoute
          exact={true}
          path="/meaningfullWord"
          component={MeaningFullWord}
        />

        <PrivateRoute
          exact
          path="/meaningfullsentence"
          component={MeaningFullSentence}
        />

        <PrivateRoute
          exact={true}
          path="/arrangenumbers"
          component={ArrangeNumbers}
        />

        <PrivateRoute
          exact={true}
          path="/moreandless"
          component={MoreAndLess}
        />

        <PrivateRoute exact={true} path="/addition" component={MathTemplate} />

        {/* <PrivateRoute
          exact={true}
          path="/answertable"
          component={AnswerTable1}
        /> */}

        <PrivateRoute
          exact={true}
          path="/answertable"
          component={TableQuestions}
        />

        <PrivateRoute
          exact={true}
          path="/fillinblanks"
          component={FillInBlanks}
        />
      </Switch>
    </div>
  );
}

export default App;
